body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(53, 51, 57);
  color: aliceblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


.game{
    max-width: 500px;
    margin: 0 auto;
}

.body{
    display: flex;
    margin: 10px;
}

img{
    width: 80px;
    height: 80px;
}

.left{
    display: flex;
    flex-wrap: wrap;
    border: 2px solid;
    width: 100%;
    height: 250px;
}
.start{
    width: 33%;
    height: 80px;
}

.right{
    display: flex;
    flex-wrap: wrap;
    border-top: 2px solid;
    border-right: 2px solid;
    border-bottom: 2px solid;
    width: 100%;
}

 .timer{
    width: 50%;
}

.number{
    width: 30%;
    margin: 3px;
    font-size: 38px;
}
.start{
    background-color: rgb(53, 51, 57);
}
.playAgain{
    text-align: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}

.btnPlay{
  margin: 0 auto;
  width: 50%;
  padding: 8px;
  color: rgb(244, 238, 238);
  font-weight: bold;
  border: 1px solid #969492;
  border-radius: 8px;
  box-shadow: 5px 5px 15px -4px #000000;
  background-color: rgb(87, 148, 234);
}
.btnPlay:hover{
  color: rgb(20, 121, 85);
}
